	  mode: 'selector',
	  directorReviewCount:''  ,//待审核计数图标
	  isDoRefresh: false
	  
    };
  },

  onPullDownRefresh() {
    // this.getUserInfo();
	  this.userDetails()
	  //获取未读信息
	  this.getNewsNum()
	  //获取默认职位
	  this.pupDefault()
	  //获取职位列表
	  this.pupList()
	  //待审核计数
	  this.reviewCount()
	  uni.stopPullDownRefresh();
    
  },

  onShow: function (e) {
	  let pages = getCurrentPages();
	  let currPage = pages[pages.length-1];
	  if (currPage.data.isDoRefresh == true){
	  	       currPage.data.isDoRefresh = false;
	  		   this.reviewCount()
	  	 }
  },
  onLoad: function (options) {
        _this = this;
		if (this.checkLogin()){
			uni.showLoading({
			  title: '加载中'
			});
			let timer = setTimeout(() => {
			  uni.hideLoading();
			}, 1000);
			//用户信息
			this.userDetails()
			//获取未读信息
			this.getNewsNum()
			//获取默认职位
			this.pupDefault()
			//获取职位列表
			this.pupList()
			this.reviewCount()
			
		}
		

  },
  components: {
