  props: {},
  methods: {
	  reviewCount:function(){
	  	let data={}
	  	let url= this.Api.directorReviewCount
	  	this.myRequest(data,url,'get').then(res => {
	  	  console.log(res);
	  	  _this.directorReviewCount= res.data.data.msg
	  	  console.log(_this.directorReviewCount)
	  	}).catch(err => {
	  	  wx.showToast({
	  	    title: err.data.errMsg,
	  	    icon: 'none'
	  	  });
	  	});
	  },
    showPicker() {
    			  this.$refs.mpvuePicker.show();
    			},
    			onConfirm(e) {
    				this.index = e.index
					// this.identityName = this.identList[this.index].label
					let id = this.identList[this.index].id
					switch(id){
							case 'SALES_DIRECTOR':
							 try{
								uni.setStorageSync('pupDefault','SALES_DIRECTOR')
							 }catch(e){
								//TODO handle the exception
								uni.showToast({
									title: '缓存出错',
									icon: 'none'
								});
								return
							 }
						    
						    this.userDetails()
						    this.arrList =arrListGM;
							this.isSpecial = true
						    this.isSeller = false;
							break;
					    case  'REGIONAL_MANAGER':
							try{
								uni.setStorageSync('pupDefault','REGIONAL_MANAGER')
								this.userDetails()
							}catch(e){
								//TODO handle the exception
								uni.showToast({
									title: '缓存出错',
									icon: 'none'
								});
								return
							}
							this.userDetails()
							setTimeout(function() {
								let temp = arrListRGbuy
								temp[0].url = temp[0].url+'?regionCode=' + _regionCode
								console.log(temp[0].url)
								this.arrList = temp;
							}, 500);
							
							this.isSpecial = true
						    this.isSeller = false;
							break;
						case 'BUY_DEPUTY':
						     try{
						     	uni.setStorageSync('pupDefault','BUY_DEPUTY')
						     }catch(e){
						     	//TODO handle the exception
						     	uni.showToast({
						     		title: '缓存出错',
						     		icon: 'none'
						     	});
						     	return
						     }
							this.userDetails()
						    this.arrList = arrListBuyB; 
						    this.isSeller = false;
							this.isSpecial = false
							break;
						case 'SELL_DEPUTY':
						    try{
						    	uni.setStorageSync('pupDefault','SELL_DEPUTY')
						    }catch(e){
						    	//TODO handle the exception
						    	uni.showToast({
						    		title: '缓存出错',
						    		icon: 'none'
						    	});
						    	return
						    }
							this.userDetails()
						    this.arrList = arrListSellB;
							this.isSpecial = false
						    this.isSeller = true; 
							break;
					}		
    			},
    			onChange(e) {
    			  this.index = e.index
    			},
    			onCancel(e) {
    			  console.log('onCancel')
    			  console.log(e);
    			},
    			selectChange(e){
    				this.index = e.detail.value
    			},
    			showSinglePicker() {
    				console.log(this.identList)
    				this.mode = 'selector'
    				this.index = [0]
    				this.$refs.mpvuePicker.show()
    			},
					
    // 获取未读消息数量
    getNewsNum: function () {
      JsyServer.getNewsNum().then(res => {
        _this.newsNum = res.data.data.count
      }).catch(err => {
        console.log("getNewsNum=err==", res);
      });
    },
	userDetails:function(){
		let data = {
			postCode: uni.getStorageSync('pupDefault')
		} 
		
		JsyServer.userDetails(data).then(res => {
		  console.log(res)
		  _this.userInfo = res.data.data
		  _regionCode = res.data.data.regionCode  //区域经理要带的区域代码
		  console.log("quyudaima",_regionCode)
		  console.log("userInfo===",_this.userInfo)
		  this.pupDefault()
		}).catch(err => {
		  console.log("getNewsNum=err==", res);
		});
	},
	
    pupDefault:function(){
			let pupDefault = uni.getStorageSync('pupDefault')
			console.log("默认身份====",pupDefault)
			if (pupDefault == 'SELL_DEPUTY' || pupDefault == 'SELLER'){
				_this.isSeller = true;
			}
			if (pupDefault == 'SALES_DIRECTOR' || pupDefault == 'REGIONAL_MANAGER'){
				_this.isSpecial = true;
			}
			switch (pupDefault){
				case "BUY_DEPUTY":
				     _this.arrList = arrListBuyB
					 break;
				case "SELL_DEPUTY":
				     _this.arrList = arrListSellB
				     break;
				case "ANALYST":
				     _this.arrList = arrListAN
				     break;
				case "SALES_DIRECTOR":
				     _this.arrList = arrListGM
					 break;
				case "REGIONAL_MANAGER":
				     _this.arrList = arrListRGbuy
					 break;
				case "BUYER":
				     _this.arrList = arrListBuyer
					 break;
				case "SELLER":
				     _this.arrList = arrListSeller
					 break;
			}
		
	},
	
    pupList:function(){
		JsyServer.pupList().then(res=>{
			console.log("身份列表",res)
			_this.identList = res.data.data.list
			 
		}).catch(err =>{
			console.log("pupList=err==", res)
		})
	},
    // 扫描二维码
    scanCode: function () {
      var that = this;
      wx.scanCode({
        success: function (res) {
          console.log("code---", res);
          console.log("path---", res.path);
          wx.navigateTo({
            url: "/" + res.path
          });
        },
        fail: function (res) {}
      });
    },
    navNewsPage: function () {
      console.log("跳转到消息页");
      wx.navigateTo({
        url: '/pages/jin-suo-yun/news'
      });
    },
    toChoicePage: function () {
      if (!wx.getStorageSync("token")) {
        return;
      }
    },
	prompt:function(url){
           if (url == ''){
			   uni.showToast({
			   	title: '开发中...',
				icon:'none',
				duration: 1000
			   });
			   return
		   }
	},
    toJump: function (e) {
      let url = e.currentTarget.dataset.url;

      if (!url || !this.popAds && url == "/pages/user-order/order") {
        wx.showToast({
          title: '开发中...',
          icon: 'none'
        });
      }
    },
  }
